.bgc-67 {
  background: url(images/page_067/fondo-corcho.jpg) top center / cover no-repeat !important;
  -webkit-print-color-adjust: exact !important;
}

.corcho-borde{
  border-width: 10px;
  border-style: solid;
  border-color: #A24410;
}

.box-shadow-67-1 {
  -webkit-box-shadow: 5px 5px 1px -1px var(--saitama);
  box-shadow: 5px 5px 1px -1px var(--saitama);
}

.box-shadow-67-2 {
  -webkit-box-shadow: 5px 5px 1px -1px var(--freezer);
  box-shadow: 5px 5px 1px -1px var(--freezer);
}

.box-shadow-67-3 {
  -webkit-box-shadow: 5px 5px 1px -1px var(--goku);
  box-shadow: 5px 5px 1px -1px var(--goku);
}

.box-shadow-67-4 {
  -webkit-box-shadow: 5px 5px 1px -1px var(--android18);
  box-shadow: 5px 5px 1px -1px var(--android18);
}

@media all and (max-width: 767px) {
  .bgc-67 {
    background: url(images/page_067/fondo-corcho.jpg) top center / cover no-repeat !important;
    -webkit-print-color-adjust: exact !important;
  }
}