    z-index: 1;
  }

  .tf-nota {
    display: flex;
    position: absolute;
    right: 60px;
    top: 960px;
    z-index: 1;
  }

  .tf-personaje-20 {
    display: flex;
    position: absolute;
    right: 80px;
