  }

  .tf-tren {
    display: flex;
    position: absolute;
    left: 60px;
    bottom: 20px;
    z-index: 1;
  }

  .tf-frase {
    display: flex;
    position: absolute;
    font-size: 16px;
    text-align: center !important;
    right: 245px;
    bottom: 80px;
    z-index: 1;
  }

  .pbpx-150 {
    padding-bottom: 120px;
  }
}

/*Aldo 830*/
.cb-block-superior-20 {
  max-width: 160px;
  border-radius: 10px;
  margin-right: auto;
  margin-left: auto;
  position: relative;
  width: calc(100% - 30px);
}

.line-dotted {
  border: 1px dashed #dedede;
}

.cb-block-arriba-20 {
  max-width: 500px;
  margin-right: auto;
  margin-left: auto;
  width: calc(100% - 30px);
}

