.cb-paper {
  position: relative;
  width: calc(100% - 100px);
  margin: 50px auto 46px;
}

.cb-paper-content {
  position: relative;
  z-index: 3;
  background: #fefcfb;
  padding: 30px 40px;
  font-size: 16px;
}

.cb-paper-sign {
    margin-top: 30px;
    text-align: right;
}

.cb-paper::before,
.cb-paper::after {
  position: absolute;
  content: '';
  height: 100%;
  width: 100%;
  background: #f1eee0;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.15);
  left: 0;
  top: 0;
}

.cb-paper::before {
  z-index: 2;
  -webkit-transform: rotate(-2deg);
          transform: rotate(-2deg);
}

.cb-paper::after {
  z-index: 1;
  -webkit-transform: rotate(2deg);
          transform: rotate(2deg);
}

.cb-lapiz{
  position: absolute;
    left: 25px;
    bottom: 70px;
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
}
.cb-carta{
  position: absolute;
    right: -35px;
    top: 10px;
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
}

