.cb-cabecera-2{
    -webkit-clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 8% 100%);
    clip-path: polygon(0 0%, 100% 0, 100% 85%, 0% 100%);
    background-color: var(--goten);
    height: 80px;
  }

.cb-block {
    border: 1px solid #d7dd3b;
    border-radius: 10px;
    margin-right: auto;
    margin-left: auto;
}
.cb-contenido-cajas {
    position: relative;
    margin-right: auto;
    margin-left: auto;
    max-width: 550px;
    width: calc(100% - 30px);
    flex-wrap: nowrap;
  }
  .cb-outline-2{
    outline: solid 4px #ffffff;
    outline-offset: -5px;
}

.line-dotted {
    border: 1px dashed #dedede;
}

.block-bottom {
    max-width: 700px;
    margin-right: auto;
    margin-left: auto;
    width: calc(100% - 30px);
}

.rounded-caja {
    border-radius: 20px 20px 0px 0px;
}

.linea {
    border-bottom: 1px dashed #dedede;
    padding-top: 5px;
    border-color:var(--dark);
}


.min-hp-35 {
  min-height: 35px !important;
}
.min-hp-70 {
  min-height: 70px !important;
}
.min-hp-140 {
  min-height: 140px !important;
}
.min-hp-175 {
  min-height: 175px !important;
}

.cb-personaje-18{
    position: absolute;
    right: 55px;
    top: -86px;
    transform: rotate(1grad);
  }

  .cb-iphone{
    position: absolute;
    left: -55px;
    bottom: 80px;

  }

  .cb-interior-caja{
    background-color: white;
    margin: -8px -8px 0px -8px;
    border-radius: 8px 8px 0px 0px;
    padding: 5px 10px;
  }

  .cb-flecha{
    padding-bottom: -2px;
}

input[type=text], input[type=password]
{
    border:none;
    border-bottom: 1px dashed #dedede;
    padding-top: 5px;
    border-color:var(--dark);
}
textarea[type=text]{  
    border:none;
    border-bottom: 1px dashed #dedede;
    padding-top: 5px;
    border-color:var(--dark);
    height: auto;
}    


@media all and (max-width: 800px) {

    .titulo-linea .linea{
        width: 100%;
    }

    .cb-iphone {
        display: none;
    }

    .cb-contenido-cajas {
        flex-wrap: wrap;
      }
      .cb-flecha{
        padding-bottom: 13px;
    }

      
}